.container {
  margin-top: 24px;
  padding: 8px; }

.analysisContainer {
  font-size: 14px;
  margin-top: 16px;
  margin-right: 12px;
  margin-left: 12px;
  color: #333; }

.analysisTitle {
  margin-top: 8px;
  margin-bottom: 6px;
  color: #666; }
  .analysisTitle + p {
    margin: 0;
    word-break: break-all; }

.notes {
  font-size: 14px;
  float: right;
  color: #1AAD19; }

.notes:active {
  color: #000; }

.content {
  margin-right: -12px;
  margin-left: -12px; }

.content button img {
  width: 112px;
  height: 112px; }

.wrongReasonContent {
  overflow: hidden;
  width: 100%; }

.wrongReasonContent button {
  font-size: 13px;
  line-height: 26px;
  float: left;
  height: 26px;
  margin-right: 8px;
  margin-bottom: 8px;
  padding-right: 5px;
  padding-left: 5px;
  text-align: center;
  color: #333;
  border: solid 1px #888;
  border-radius: 3px;
  outline: none; }

.addLabels {
  font-size: 14px;
  margin: 0;
  padding: 0;
  cursor: pointer;
  text-decoration: underline;
  color: #333;
  border: none;
  outline: none;
  background-color: transparent; }



