.answerContent {
  position: relative;
  display: block;
  overflow: hidden;
  min-height: 40px;
  margin-right: 12px;
  margin-left: 12px;
  border-bottom: solid 2px #C8C8C8;
  outline: none; }

.circleOrder {
  font-size: 12px;
  line-height: 20px;
  position: absolute;
  top: 10px;
  left: 2px;
  width: 20px;
  height: 20px;
  text-align: center;
  color: #383F4A;
  border-radius: 10px;
  background-color: #FDBE41; }

.rightAnswer {
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  margin: 16px 12px 0;
  letter-spacing: normal;
  color: #262626; }



