* {
  margin: 0;
  padding: 0; }

html,
body,
:global(#app),
:global(#app) > div {
  height: 100%; }

body > .pace > *:nth-child(1) {
  background-color: #3B9E46; }
  body > .pace > *:nth-child(1) > div {
    -webkit-box-shadow: 0 0 10px red, 0 0 5px red;
            box-shadow: 0 0 10px red, 0 0 5px red; }

body > .pace > *:nth-child(2) {
  border-top-color: #3B9E46;
  border-left-color: #3B9E46; }



